.loader_background-l {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader_background-m {
  height: calc(100vh - 152px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  animation: 1s loader infinite linear;
}

.loader-size-l {
  width: 48px;
  height: 48px;
  transform: rotate(-54deg);
  border-right: 5.5px solid #6C757D;
  border-top: 5.5px solid #6C757D;
  border-left: 5.5px solid #6C757D;
  border-bottom: 5.5px solid transparent;
  border-radius: 50px;
}

.loader-size-m {
  width: 31px;
  height: 31px;
  transform: rotate(-54deg);
  border-right: 4.5px solid #6C757D;
  border-top: 4.5px solid #6C757D;
  border-left: 4.5px solid #6C757D;
  border-bottom: 4.5px solid transparent;
  border-radius: 50px;
}

.loader-size-s {
  width: 14px;
  height: 14px;
  transform: rotate(-54deg);
  border-right: 3px solid #6C757D;
  border-top: 3px solid #6C757D;
  border-left: 3px solid #6C757D;
  border-bottom: 3px solid transparent;
  border-radius: 50px;
  margin: 10px auto;
}

@keyframes loader {
  0%{
    transform: rotate(0);
  }
  100%{
    transform: rotate(360deg);
  }
}